import React from 'react';
import Link from 'next/link';

class PostItem extends React.Component {

sizeMemory = (size, page = 2) => {
  if (size < 0) {
    return '';
  }
  if (size === 0) {
    return 0 + ' B';
  }
  if (size < 1024) {
    return size.toFixed(0) + ' B';
  } else if (size >= 1024 && size < 1024 * 1024) {
    return (size / 1024).toFixed(page) + ' KB';
  } else if (size >= 1024 * 1024 && size < 1024 * 1024 * 1024) {
    return (size / (1024 * 1024)).toFixed(page) + ' MB';
  } else if (size >= 1024 * 1024 * 1024 && size < 1024 * 1024 * 1024 * 1024) {
    return (size / (1024 * 1024 * 1024)).toFixed(page) + ' GB';
  } else if (size >= 1024 * 1024 * 1024 * 1024) {
    return (size / (1024 * 1024 * 1024 * 1024)).toFixed(page) + ' TB';
  }
}


	render() {
    console.log(this.sizeMemory(11987));
    var x = this.props.id;
		const url = `/post?id=${x}`;
    return(
			<div className="col-md-12">
        <div className="post post-row">
         	<Link href={url}><a className="post-img"><img src="../static/img/post-6.jpg" /></a></Link>
          <div className="post-body">
            <div className="post-meta">
              <a className="post-category cat-2" href="category.html">JavaScript</a>
          		<span className="post-date">March 27, 2018</span>
		        </div>
        		<h3 className="post-title"><Link href={url}><a>{this.props.title}</a></Link></h3>
            <p>{this.props.body}</p>
        	</div>
      	</div>
      </div> 
		);
	}
}

export default PostItem;